.project-card{
    width: 400px;
} 

.project-cont{
    display: grid;
    grid-template-columns:repeat(auto-fill,minmax(400px,max-content));
    grid-template-rows: repeat(auto-fill);
    margin: auto;
    justify-content: center;
    grid-gap: 40px 40px;
    padding-bottom: 30px;
}

.Project-item{
    /* border: 1px solid red; */
    /* box-shadow: 0 0 10px 10px #f4f6f8; */
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    height: 460px;
    width: 380px;
    border-radius: 10px;
    cursor: pointer;
    background-color: #23283E;
    padding: 10px;
}
.Project-item:hover{
    box-shadow: 0 0 10px 10px #102d4b;
    transition: var(--transition) ;
    transform: translateX(1rem);
}
.Project-image{
    margin: auto;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    width: 95%;
    /* border: 1px solid green; */
}
.project-title{
    font-size: 20px;
    line-height: 28px;
    margin-top: 8px;
}
.project-description{
    margin: 5px;
    text-align: left;
    font-size: 16px;
    font-weight: 300;
    /* border: 1px solid red; */
    line-height: 24px;
}

.project-tech{
    text-align: left;
    font-size: 14px;
    margin: 5px;
    font-weight: 300;
    line-height: 24px;
    /* border: 1px solid red; */
}

.Project_btn{
    justify-content: center;
    gap: 10px;
    display: flex;
}
.primary{
    background-color: #90A0D9;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}