.about{
    display: flex;
    width: 100%;
    margin: auto;
    text-align: center;
    height: auto;
    font-family: "Poppins,Sans-serif";
    gap: 20px;
    padding-bottom: 30px;
}

.img-cont{
    margin: auto;
    /* border: 1px solid white; */
    
}

.bio-cont{
    text-align: left;
    /* border: 2px solid red; */
    margin: auto;
    color: #D6D6D6;
    font-size: 18px;
    font-family: "Poppins,Sans-serif";
    width: 60%;
    font-weight: 300;
    line-height: 24px;
}

.dummy{
    width: 350px;
    height: 400px;
    margin: auto;
    border-radius: 10px;
    margin-bottom: 30px;
    box-shadow: 0 0 10px 10px #2f373f;
    cursor: pointer;
}
.dummy:hover{
    box-shadow: 0 0 10px 10px #5f6368;
    transition: var(--transition) ;
    transform: translateY(-1rem);
}
  
  