*{
    font-family: 'Poppins,sans-serif';
}

.dino{
  width: 50px;
  height: 50px;
  /* border: 1px solid red; */
  margin-top: -15px;
}
#data{
    width: 100%;
    margin: auto;
    display: block;
    z-index: 2;
}

@media  (min-width: 900px){
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        font-family: 'Poppins';
        box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
        display: block;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(10px);
    }
    
    
    #nav-menu .nav-list{
      justify-content:  right;
      padding: 20px;
      
    }
    
    .items{
        gap: 40px;
    }
    .bread{
      font-size: 20px;
      font-weight: 600;
    }
}

@media  (min-width: 599px)  and (max-width: 899px) {
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        display: block;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(8px);
    }
    
    
    #nav-menu .nav-list{
      justify-content: center;
      padding: 5px;
      margin: auto;
      padding:"25px 0px 25px 0px" ;
    }
    
    .items{
        gap: 15px;
    }

    .bread{
      font-size: 22px;
      font-weight: 500;
      margin: auto;
    }
}
@media (max-width: 598px) {
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        display: block;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(8px);
    }
    
    
    #nav-menu .nav-list{
      justify-content: center;
      margin: auto;
      padding: 20px 0px 10px 0px;
    }
    
    .items{
        gap: 8px;
    }

    .bread{
      font-size: 14px;
      font-weight: 300;
      margin: auto;
    }
}