*{
    font-family: 'Poppins,sans-serif';
}

#home{
    font-family: "Poppins,Sans-serif";
    margin: auto;
    width: 100%;
    height: 500px;
    overflow-x: hidden;
}

.back{
    width: 100%;
    height: 100%;
    -webkit-filter: blur(5px);
    filter: blur(5px);
}

#home .content{
    position: absolute;
    justify-content: center;
    text-align: center;
    border-radius: 15px;
    margin: auto;
    margin-top: -35%;
    width: 100%;
    z-index: 2;
}

#about{
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 100%, rgba(0,212,255,1) 100%, rgba(1,187,239,1) 100%); */
    background-color: 23283E;
    width: 100%;
    overflow-x: hidden;
}

#projects{
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 100%, rgba(0,212,255,1) 100%, rgba(1,187,239,1) 100%); */
    background-color: 23283E;
    width: 100%;
    margin: auto;

}

#contact{
    background-color: 23283E;
    width: 100%;
}

.head-image{
    width: 250px;
    height: 250px;
    margin: auto;
}
  
#skills{
    width: 100%;
    margin: auto;
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 100%, rgba(0,212,255,1) 100%, rgba(1,187,239,1) 100%); */
    background-color: 23283E;
}

@media (min-width:501px) and  (max-width: 1000px){
    #home {
        display: block;
        justify-content: center;
        margin: auto;
    }


}
@media  (max-width: 500px){
    #home {
        display: block;
        justify-content: center;
        padding: 0;
        margin: auto;
    }
}