*{
    font-family: 'Poppins,sans-serif';
}
#skills-container{
    width: 100%;
    margin: auto;
    justify-content: center;
    align-items: center;
}

.tools-box .skill-logo{
    margin: auto;
    width: 100px;
    height: 100px;
    margin-bottom: 5px;
}

.tools-box{
    width: 120px;
    margin-bottom: 70px;
    height: 150px;
    box-shadow: 0 0 20px 10px #ffffff;
    justify-content: center;
    padding-top: 15px;
    border-radius: 10px;
    align-items: center;
    cursor: pointer;
    background-color: black;
    /* font-size: 16px; */
}

.tools-box:hover{
    box-shadow: 0 0 20px 10px #2f568d;
    transition: var(--transition) ;
    transform: translateY(-1rem);
    /* background-color: white; */
    color: #0ba0db;
}